import React, { useState, useEffect } from 'react';
import logo2 from '../assets/logo2.png';
import copy from '../assets/copy.png';
import PackageJson from '../../package.json';
import { useLocation } from 'react-router-dom';

export default function Toast() {
    const [open, setOpen] = useState(false);
    const location = useLocation();
    let timer;

    const openToast = () => {
        if (open) return;
        setOpen(true);

        clearTimeout(timer);

        //          
        const rootUrl = PackageJson.homepage;
        console.log(rootUrl);
        console.log(location.pathname);

        handleCopyClipBoard(`${rootUrl}${location.pathname}`);

        timer = setTimeout(() => {
            setOpen(false);
        }, 3000);
    };

    const closeToast = () => {
        setOpen(false);
    };

    const handleCopyClipBoard = async (text) => {
        try {
            await navigator.clipboard.writeText(text);
            alert("  .");
        } catch (err) {
            console.log(err);
        }
    }

    useEffect(() => {
        //  Alpine.js     .
        // Alpine.js React    .
    }, []);

    return (
        <div className="w-full h-full flex items-center justify-center mb-7">
            {open && (
                <button
                    type="button"
                    onClick={closeToast}
                    className="fixed right-4 top-20 z-50 rounded-md bg-green-500 px-1 py-1 text-white transition hover:bg-green-600 text-sm"
                    style={{ transitionDuration: '300ms' }}
                >
                    <div className=" flex items-center space-x-2">
                        <span className="text-3xl">
                            <i className="bx bx-check"></i>
                        </span>
                        <p className="font-bold">
                             ! <br />  Crtl + V     .
                        </p>
                    </div>
                </button>
            )}

            <button
                type="button"
                className="rounded-md bg-green-500 px-4 py-2 text-2xl font-bold text-white transition"
            >
                <div className="flex items-center space-x-4">
                    <img
                        className="w-16 h-16"
                        src={logo2}
                        alt="2"
                    />
                    <span>
                                  <br />
                        <p className="text-base font-medium">
                                        .
                        </p>
                    </span>
                    <button
                        type="button"
                        onClick={openToast}
                        className="ml-2 rounded-md bg-white px-4 py-2 text-gray-700 transition hover:bg-gray-200 text-base font-semibold"
                    >
                          
                    </button>
                </div>
            </button>
        </div>
    );
}
